
import { ref, defineComponent } from "vue";

export default defineComponent({
    name: "CustomPopup",
    props: {
        width: {
            type: Number,
            defulat: 500,
        },
        height: {
            type: Number,
            default: 500,
        },
    },
    setup () {
        const isShow = ref(false);

        function openPopup () {
            isShow.value = true;
        }

        function closePopup () {
            isShow.value = false;
        }

        return {
            isShow,
            openPopup,
            closePopup,
        };
    }
});
